.container {
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  font-size: 16px;
  height: 22px;
  position: relative;
  padding-left: 30px;
  margin-bottom: 12px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.container input {
  cursor: pointer;
  height: 0;
  opacity: 0;
  position: absolute;
  width: 0;
}

.checkmark {
  background-color: #eee;
  height: 22px;
  left: 0;
  position: absolute;
  top: 0;
  width: 22px;
}

.container:hover input ~ .checkmark {
  background-color: #ccc;
}

.container input:checked ~ .checkmark {
  background-color: #4286f4;
}

.checkmark:after {
  content: '';
  display: none;
  position: absolute;
}

.container input:checked ~ .checkmark:after {
  display: block;
}

.container .checkmark:after {
  border: solid white;
  border-width: 0 2px 2px 0;
  height: 10px;
  left: 7px;
  top: 3px;
  width: 5px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

body {
  box-sizing: border-box;
  height: 100vh;
  margin: 0;
  padding: 100px 0 0;
  font-family: sans-serif;
}

